<template>
    <div class="box">
        <div  class="search">
            <div class="search_box" >
                <i class="fa fa-search"></i>
                <input :@keyup="flag" type="text" placeholder="请输入影城名称" v-model="search">
            </div>
            <router-link to="./"><div class=" cancel">取消</div></router-link>  
        </div>
        <div
         class="near"
         v-if="!searchData"
         >
                <p>离你最近</p>
                <mt-button>保利国际影城北京天安门店</mt-button>
                <mt-button>北京大观楼影台</mt-button>
                <mt-button>北京西单电影院西单店</mt-button>
                <mt-button>北京地质影堂影院</mt-button>
                <mt-button>北京中华电影院中华店</mt-button>
        </div>
        <div v-for="list in searchData"
        :key="list.cinemaId"
        >
            <ul>
                <li > 
                    <span>{{list.name}}</span>
                    <a>￥{{list.lowPrice}}</a>
                    <p>{{list.address}}</p>
                </li>
            </ul>
        </div>
   </div>
</template>

<script>
export default {
  data () {
      
    return {
    	search:'',
    	searchData: '',
		
            products:[]
        }
  },
       
    computed: {
        
        flag:function(){
        if(this.search == ""){
            this.searchData ="";
            
        }else{
            this.btn()
            
        }
        
      }
        
    },
  
  methods:{
  	btn(){
  		var search = this.search;
			if (search) {
			 this.searchData =  this.products.filter(function(product) {
				return Object.keys(product).some(function(key) {
			  	return String(product[key]).toLowerCase().indexOf(search) > -1
			})
			})
			}
			
      }
      },
       created(){
            this.$http.get("/api/mz/list/").then(res=>{
            this.products = res.data.data.object_list;
            console.log(res.data)
        })
        },
      
  
  
}

</script>


<style lang="scss">
 .search{
     width: 3.75rem;
     margin-top:10px;
      display: flex;
        justify-content: space-between;
        align-items: center;
        padding:0 15px;
     .search_box{
    margin-left:20px;
    background:#f4f4f4;
    width:70%;
    height:30px;
    float:left; 
    border-radius:7px;
    input{
        border:none;
        background:#f4f4f4;
        padding-left:10px;
    }
    i{
        padding-left:10px;
    }
    }
    .cancel{
    float:right;
    font-size:16px;
    height:30px;
    width: 50px;
    text-align:center

}

 }

ul li {
   
    float:left;
  list-style: none;
  span {
    margin: 0 20px;
    line-height: 30px;
    color:#191a1b;
    font-size:15px;
    float:left
    }
  a{
      color:#ff5f16;
      font-size:15px;
      padding-right:20px;
      float:right
  }
  p{
      float:left;
      color:#797d82;
      font-size:12px;
      padding-left:20px;
  }
 
}
.near{
    p{
        margin: 20px 0 0 20px;
    }
    .mint-button{
    font-size: 12px;
    height: 30px;
    margin:10px 0 0 20px;
    }
}




</style>